import React from "react"
import { Helmet } from "react-helmet"
import { useI18next } from 'gatsby-plugin-react-i18next'

const SiteHat = () => {
  const { language } = useI18next()

  return (<Helmet>
     <html lang={ language } />
      <title>DockOvpn</title>
      <meta charSet="utf-8" />
      <meta 
        name="description" 
        content="Out of the box stateless openvpn server docker image which starts in just a few seconds and doesn't require presistent storage."
      />
      <meta property="og:title" content="DockOvpn"/>
      <meta property="og:type" content="website" />
      <meta property="og:description" content="Blazingly fast VPN"/>
      <meta property="og:image" content="https://dockovpn.io/thumbnail.png"/>
      <meta property="og:image:width" content="800" />
      <meta property="og:image:height" content="600" />
      <meta property="og:url" content="https://dockovpn.io/index.html"/>
      <meta name="twitter:card" content="summary_large_image"/>
      <meta property="og:site_name" content="DockOvpn"/>
      <meta name="twitter:image:alt" content="Dockerized OpenVPN server"/>
      <meta name="twitter:site" content="@dockovpn"/>
    </Helmet>)
}

export default SiteHat