import React, { useMemo, useState } from "react"
import { MdGTranslate } from 'react-icons/md'
import * as styles from "./languageselector.module.css"
import {Link, useI18next} from 'gatsby-plugin-react-i18next';

const LanguageSelector = ({ docs }) => {
    const [isOpen, setOpen] = useState(false)
    const docsClassName = (docs === true) ? " " + styles.docs : ""
    const openClasName = (isOpen === true) ? " " + styles.open : ""
    const wrapperClassNames = styles.langWrapper + docsClassName + openClasName

    const {languages, originalPath} = useI18next()

    const fullLangByLocale = useMemo(() => {
        const innerMap = new Map()
        innerMap.set('ru', 'Русский')
        innerMap.set('en', 'English')

        return innerMap
    }, [])

    return (
        <div className={ wrapperClassNames } onClick={ () => setOpen(!isOpen) } onMouseLeave={ () => {
            if (isOpen)
                setOpen(false)
        } } >
            <p><span className={ styles.langIcon }><MdGTranslate /></span><span className={ styles.langTitle }> Languages</span></p>
            {isOpen && 
                <ul>
                    { languages.map(lang => {
                        return (
                            <li key={ lang }>
                                <Link to={originalPath} language={lang}>
                                    { fullLangByLocale.get(lang) }
                                </Link>
                            </li>
                        )
                    })}
                </ul>
            }
        </div>
    )
}

export default LanguageSelector